.google-drive--iframe-size {
  border: none;
  width: 250px;
  height: 800px;
}

iframe::-webkit-scrollbar {
  display:none;
}

.iframe--div-scaled-up {
  padding-top: 70px;
  padding-bottom: 30px;
  -webkit-transform: scale(1.3);
      -ms-transform: scale(1.3);
          transform: scale(1.3);
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
  overflow: hidden;
}



